<template>
  <div class="find">
    <div class="swiper" v-if="banner.itemList">
      <van-swipe :autoplay="5000" :show-indicators="false" indicator-color="white">
        <van-swipe-item v-for="(item, index) in banner.itemList" :key="index">
          <img :src="item.data.image" alt />
        </van-swipe-item>
      </van-swipe>
    </div>
    <template v-if="classify">
      <TitleItem :titleInformation="{name:'热门分类',other:'查看全部'}" @goToMonreClassify="goToMonreClassify"></TitleItem>
      <div class="calssify-list">
        <ClassifyItemCard v-for="(item, index) in classify" :classItem="item" :key="index"></ClassifyItemCard>
      </div>
    </template>
    <div class="day-rank" v-if="DayRankArr">
      <div class="title">
        <div class="title-name">今日榜单</div>
        <div class="other" @click="getMoreRank">查看全部</div>
      </div>
      <RankItem v-for="(item, index) in DayRankArr" :item="item" :key="index+`d`"
        @RankItemClick="RankItemClick($event)"></RankItem>
    </div>
    <div v-else class="rankloading">加载中</div>
  </div>
</template>

<script>
  import TitleItem from "@/components/TitleItem.vue";
  import ClassifyItemCard from "@/components/ClassifyItemCard.vue";
  import RankItem from "@/components/RankItem.vue";

  export default {
    name: "find",
    data() {
      return {
        findData: [],
        DayRankArr: []
      };
    },
    computed: {
      banner() {
        let n = {};
        if (this.findData) {
          this.findData.forEach((element, index) => {
            if (index == 0) {
              n = element.data;
            }
          });
        }

        return n;
      },
      classify() {
        return this.$store.state.Classify.slice(0, 6);
      }
    },
    methods: {
      getMoreRank() {
        this.$router.push('/rankList')
        this.$store.commit('setShowBottomBar', false)
      },
      RankItemClick($event) {
        this.$router.push(`/playingdetailpage?id=${$event.data.id}`);
        this.$store.commit(`setPlayingPageDate`, $event)
        this.$store.commit('setShowBottomBar', false)
      },
      goToMonreClassify() {
        this.$router.push({
          path: "/moreclassify"
        });
        this.$store.commit("setShowBottomBar", false);
        this.$store.commit("setIsInHome", false);
      }
    },
    created() {
      this.axios
        .get(
          `https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v5/index/tab/discovery`
        )
        .then(res => {
          this.findData = res.data.itemList;
        });
      this.axios
        .get(
          `https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v3/ranklist?num=10&strategy=historical&udid=26868b32e808498db32fd51fb422d00175e179df&vc=83`
        )
        .then(res => {
          this.DayRankArr = res.data.itemList.slice(0, 6)
          this.$store.commit("setDayRank", res.data.itemList);
          
          
          this.$store.commit("setRelevant", res.data.itemList);
          
        });
    },
    components: {
      TitleItem,
      ClassifyItemCard,
      RankItem
    }
  };
</script>

<style lang="less" scoped>
  .find {
    .rankloading {
      text-align: center;
      color: #6b6b6b;
    }

    .van-swipe {
      overflow: hidden;

      .van-swipe__track {
        .van-swipe-item {
          img {
            width: 100%;
            display: block;
          }
        }
      }
    }

    .calssify-list {
      column-count: 3;
      column-gap: 3px;

      .classify-item-card {
        margin-bottom: 3px;
      }
    }

    .day-rank {
      .title {
        display: flex;
        padding: 10px 0;

        div {
          flex: 1;

          &.title-name {
            font-weight: bold;
            font-size: 18px;
          }

          &.other {
            font-size: 14px;
            color: #1a73e8;
            text-align: right;
            line-height: 24px;
          }
        }
      }
    }
  }
</style>